body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: white;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.App {
  text-align: center;
  animation: fadeIn 1.2s ease-in;
}

/* Subtle fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

input, button {
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ccc;
  outline: none;
}

input {
  padding: 10px;
  width: 280px;
  background-color: #fff;
  color: #000;
  margin-right: 12px;
}

button {
  background-color: #ff0000;
  color: white;
  cursor: pointer;
  padding: 10px 24px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #cc0000;
}
